.main_section{
	background-color: aqua;
	width: 100vw;
	height: 100vh;
	z-index: -1;
}
.main_overlay-container{
	/*
	position:  absolute;
	left: 50%;
	bottom: 5%;
	transform: translate(-50%, -5%);
	*/
	width: 40%;
	height: 40%;
	background-color: aquamarine;
	flex-grow: 1;
}
.main_center_layout{
	/*
	display: grid;
	grid-template-rows: auto 350px;
	*/
	display:  flex;
	flex-grow: 1;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	padding: 50px;
	height: 95%;
	
	position: absolute;
	top: 5%;
	left: 50%;
	transform: translate(-50%, -5%);
	background-color: brown;
}

.main_center_layout * {
	margin: 1%;
}

.item-container{
	width: 150px;
	height: 100px;	

	border-radius: 5px;
	text-align: center;
}
.main_item-container{
	display: grid;
	grid-template-columns: 200px  200px   200px   200px  ;	
	width: 99%;
	height: 70%;
	background-color: beige;
	
	
	padding:  25px;

}
.main_footer_panle{
	flex-grow: 0;
}
.main_item{		
	justify-content: center;
	align-items: center;
}

.red{
	background-color: red;
}
.blue{
	background-color: blue;
}